@import "./variables.scss";

.home {
    /* background-image: url("./assets/bg-home.png"); */
    background-position: top center;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size:cover;
}

#intro {
    height: calc(75vh - 80px);
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 0 64px;

    h1{
        margin-bottom: 24px;
    }

    h2, a {
        font-family: $text-hand;
    }
}

#works {
    width: 100vw;
    padding: 32px 16px;
}

.work-row {
    display: flex;
    justify-content: center;
    padding: 32px 0;
}

.work-card {
    width: calc(50% - 32px);
    padding: 0 16px;
}

#indochat-color { 
    background-color:#f07b7b;
}

#amzmusic-color {
    background-color: #14191A;
}

.card-img-wrap {
    display: flex;
    justify-content: center;
    align-items: center;
    aspect-ratio: 3 / 2; 
    height: auto;
    overflow: hidden;
    border-radius: 16px;
    margin-bottom: 12px;

    img {
        display: block;
        max-width: 100%;
    }
}

.work-tag {
    margin-bottom: 4px;
}

#experience {
    padding: 16px 64px;

    H2 {
        margin-bottom: 20px;
    }
}

.experience {
    display: flex;
    gap: 32px;
}

@media (max-width: 720px) {
    #works {
        padding: 0;
    }

    .work-row {
        flex-direction: column;
        align-items: center;
        padding:0;
    }
    .work-card {
        width: calc(100vw - 40px);
        padding: 20px 0;
    }
    .card-img-wrap {
        aspect-ratio: 4 / 3; 
        width: 100%;
        height: auto;
    }
}